@media (min-width: 411px) and (max-width: 820px) {
    .header {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-end;

      height: 60px;

     
    }
    
  }

  

  @media only screen  and (max-width: 410px) {
   
    .header{
        display: none;
    }
    .ThirdNav{
        display: block;
       
    }
    
    .menu{
        /* border: 1px solid blue; */
